/*=========================
  Colors
===========================*/

$primary-bg-color: #fff;
$secondary-bg-color: #f9f9f9;
$tertiary-bg-color: #d9d9d9;
$inverted-bg-color: #000;
$accent-bg-color: #518581;

$primary-text-color: #151411;
$secondary-text-color: #afadb5;
$accent-text-color: $accent-bg-color;
$inverted-text-color: $primary-bg-color;

$primary-border-color: #f3f3f3;
$accent-border-color: $accent-bg-color;

/*=========================
  Shadows
===========================*/

$primary-shadow: 0px 4px 20px rgba(175, 173, 181, 0.2);

/*=========================
  Fonts
===========================*/

$primary-font: "Eudoxus Sans", sans-serif;

/*=========================
  Breakpoints
===========================*/

$mobile: "(max-width: 768px)";
