@import "@styles/variables.scss";

/*=========================
  Badge
===========================*/

.badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 30px;
  padding: 2px 16px;
  border-radius: 15px;
  color: $accent-text-color;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3em;
  background: $secondary-bg-color;

  @media #{$mobile} {
    min-height: 26px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 12px;;
  }
}
